.footer {
  margin-top: 80px;
  background-color: rgba(0, 0, 0, 0.9);
  .footer-container {
    padding: 50px 0;
    margin: auto;
    width: 80%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 50px;
    @media (max-width: 970px) {
      flex-direction: column;
      align-items: center;
    }
    .footer-item {
      width: 40%;
      @media (max-width: 970px) {
        width: 100% !important;
      }
      &:first-child {
        width: 300px;
      }
      a {
        img {
          width: 95%;
          object-fit: cover;
          object-position: center;
        }
      }
      h6 {
        font-size: 16px;
        color: red;
        margin: 0 0 10px 0;
      }

      .item-flex {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        .flex1 {
          width: 45%;
          a {
            text-decoration: none;
            h6 {
              font-size: 16px;
              color: #fff;
              margin: 10px 0;
            }
          }
        }
      }
      .item-text {
        a {
          text-decoration: none;
          h6 {
            color: #fff;
          }
        }
      }
    }
  }
  .logo {
    color: #fff;
  }
  .copy-cont {
    background-color: black;
    padding-top: 20px;
    padding-bottom: 20px;
    .copywrite {
      width: 60%;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 970px) {
        flex-direction: column-reverse;
        width: 90%;
      }
      .copy-flex {
        width: 50%;
        @media (max-width: 970px) {
          width: unset !important;
          text-align: center;
          margin: 30px;
        }
        p {
          font-size: 13px;
          margin: 0;
          color: #fff;
        }
      }
      .copy-link {
        display: flex;
        align-items: center;
        gap: 10px;
        @media (max-width: 970px) {
          flex-direction: column;
        }
        a {
          text-decoration: none;
          font-size: 14px;
          font-weight: 600;
          color: #fff;
        }
      }
    }
  }
}
