.news-flex {
  margin: 30px 0;
  display: flex;
  width: 100%;
  align-items: flex-start;
  justify-content: space-between;
  padding-bottom: 30px;
  border-bottom: 1px solid #ccc;
  @media (max-width:720px) {
    display: block;
  }
  .image-container {
    width: 100%;
    margin-top: 20px;
    position: relative;
    img {
      width: 100%;
      height: 380px;
      object-fit: cover;
      object-position: center;
    }
    &::after {
      content: "";
      background-color: rgba(0, 0, 0, 0.5);
      position: absolute;
      inset: 0;
    }
    .text-container {
      position: absolute;
      z-index: 1000;
      bottom: 10px;
      margin: 0;
      padding: 40px;
      h2 {
        margin: 0;
        font-size: 32px;
        line-height: 40px;
        font-weight: 700;
        color: #fff;
      }
      .nation-badge {
        margin: 0 0 10px;
        font-size: 12px;
        font-weight: 700;
        padding: 0 5px;
        background-color: red;
        color: #fff;
        display: inline-block;
      }
      .detail-flex {
        margin-top: 10px;
        color: #fff;
        display: flex;
        gap: 8px;
        font-size: 14px;
        line-height: 22px;
        font-weight: 600;
        a {
          text-decoration: none;
          color: #ed1c15;
        }
        .svg-flex {
          display: flex;
          align-items: center;
          gap: 5px;
        }
      }
    }
  }

  .img {
    width: 35%;
    height: 200px;
    @media (max-width:720px) {
       width: 100%;
      }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }
  .item-body {
    width: 62%;
    @media (max-width:720px) {
       width: 100%;
      }
    .flex{
      float: right;
      display: flex;
      gap: 20px;
      .fa{
        color: green;
        cursor: pointer;
        span{
          margin-left: 5px;
        }
        &.fa-trash {
            color: red;
          }
      }
     
    }
    a {
      text-decoration: none;
      color: black;
      h2 {
        font-size: 22px;
        line-height: 30px;
        padding-right: 5px;
        margin: 0;
      }
      &:hover {
        color: red;
        transition: 0.8s;
      }
    }
    .time-bottom {
      margin-top: 10px;
      color: #ccc;
      display: flex;
      gap: 8px;
      font-size: 14px;
      line-height: 22px;
      font-weight: 700;
      a {
        text-decoration: none;
        color: #ed1c15;
        font-weight: 700;
      }
      .svg-flex {
        display: flex;
        align-items: center;
        gap: 5px;
      }
    }
    p {
      font-size: 15px;
      margin: 20px 0 0 0;
    }
  }
}
