.entertainment {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 60px;
  @media (max-width: 970px) {
    width: 90% !important;
  }
  h1 {
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    margin: 0;
    padding-bottom: 10px;
    border-bottom: 1px solid #000000;
  }
  .flex-container {
    margin-top: 30px;
    display: flex;
    align-items: stretch;
    @media (max-width: 970px) {
      flex-direction: column;
    }
    .item1 {
      cursor: pointer;
      width: 50%;
      height: 400px;
      border-right: 2px solid #ffffff;
      position: relative;
      @media (max-width: 970px) {
        width: 100% !important;
        height: 510px;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
      &::before {
        content: "";
        position: absolute;
        inset: 0;
        background-color: rgba(0, 0, 0, 0.6);
      }
      .text-container {
        position: absolute;
        z-index: 1000;
        bottom: -10px;
        margin: 0;
        padding: 40px;
        h3 {
          margin: 0;
          font-size: 26px;
          line-height: 40px;
          font-weight: 700;
          color: #fff;
        }
        .nation-badge {
          margin: 0 0 10px;
          font-size: 12px;
          font-weight: 700;
          padding: 0 5px;
          background-color: red;
          color: #fff;
          display: inline-block;
        }
        .detail-flex {
          margin-top: 10px;
          color: #fff;
          display: flex;
          gap: 8px;
          font-size: 14px;
          line-height: 22px;
          font-weight: 600;
          a {
            font-weight: 600;
            text-decoration: none;
            color: #fff;
          }
          .svg-flex {
            display: flex;
            align-items: center;
            gap: 5px;
          }
        }
      }
    }
