@import url("https://fonts.googleapis.com/css2?family=Beau+Rivage&family=Charis+SIL:ital,wght@0,400;0,700;1,400;1,700&family=Fira+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto+Slab:wght@700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
.witness {
  // margin-bottom: 50px;
  // display: block;
  font-family: "Poppins";
  width: 60%;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  @media (max-width: 970px) {
    width: 90% !important;
  }
  .flex-container {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    margin-top: 20px;
    align-items: flex-start;
    @media (max-width: 970px) {
      flex-direction: column;
    }
    .item {
      width: 31%;
      @media (max-width: 970px) {
        width: 100% !important;
      }
      h1 {
        font-size: 16px;
        line-height: 22px;
        color: #000000;
        margin: 0;
        padding-bottom: 10px;
        border-bottom: 1px solid #000000;
      }
      .item-body {
        margin-top: 20px;
        .image-container {
          position: relative;
          width: 100%;
          height: 220px;
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
          }
          &::after {
            position: absolute;
            content: "";
            inset: 0;
            background: rgba(0, 0, 0, 0.3);
          }
          .link-container {
            display: block;
          }
          .text-container {
            position: absolute;
            z-index: 1000;
            bottom: -10px;
            margin: 0;
            padding: 20px;
            h3 {
              margin: 0;
              font-size: 18px;
              line-height: 20px;
              font-weight: 700;
              color: #fff;
            }
            .detail-flex {
              margin-top: 10px;
              color: #fff;
              display: flex;
              gap: 8px;
              font-size: 14px;
              line-height: 22px;
              font-weight: 600;
              a {
                font-weight: 600;
                text-decoration: none;
                color: #fff;
              }
              .svg-flex {
                display: flex;
                align-items: center;
                gap: 5px;
              }
            }
          }
        }
        .body-text {
          margin-top: 20px;
          .text-flex {
            margin-bottom: 10px;
            display: flex;
            align-items: flex-start;
            gap: 10px;
            a {
              text-decoration: none;
              h2 {
                font-size: 16px;
                line-height: 20px;
                font-weight: 500;
                margin: 0;
                color: rgba(0, 0, 0, 0.9);
                &:hover {
                  color: rgba(250, 0, 0, 0.7);
                  transition: 0.5s;
                }
              }
            }
            padding-bottom: 15px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.2);
          }
        }
      }
    }
  }
}
